import React from 'react';
import { Route, HashRouter } from 'react-router-dom';
import './App.css';
import Table from './Table';
import SequenceMath from './SequenceMath';
import TaylorDiagram from './TaylorDiagram';
import Documentation from './Documentation';
import Nav from './Nav';

/*
**
Autor: Samuel Sepei
Dtum: 10.5.2018
Komponent: App
**
*/

/*
Hlavn komponent aplikcie, m na starosti prepnanie medzi asami aplikcie.
Pouvam HashRouter a Route na navigciu medzi jednotlivmi asami aplikcie.
Tieto komponenty zabezpeuj,  
e strnka sa nepotrebuje obnovi pri navigovan na in as aplikcie.
*/

class App extends React.Component {
  render() {
    return (
      <HashRouter>
        <div>
          <Nav />
          <div className="content">
            <Route exact path="/" component={Table} />
            <Route exact path="/math" component={SequenceMath} />
            <Route exact path="/taylor" component={TaylorDiagram} />
            <Route exact path="/docs" component={Documentation} />
          </div>
        </div>
      </HashRouter>
    );
  }
}

